/* navbar-wrapper css start */
.navbar-wrapper {
  position: relative;
  background: #ffffff;
  padding: 15px 30px;
  margin-left: 325px;
  border-bottom: 1px solid #dee4ec;
  box-shadow: 0px 2px 5px 0px #cdd4e7;
  margin-right: 30px;
  margin-top: 15px;
  @include border-radius(10px);
  @include d-flex;
  transition: all 0.5s cubic-bezier(0.40, -0.25, 0.25, 1.1);
  @media (max-width: 991px) {
    margin-left: 0; }
  @media (max-width: 575px) {
    padding: 10px 10px; } }
.navbar__left {
  @include d-flex; }
.navbar-search {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  @include d-flex;
  background-color: #f5f5f5;
  padding-right: 10px;
  align-items: center;
  z-index: 99999;
  opacity: 0;
  visibility: hidden;
  @include transition(all 0.3s);
  &.active {
    opacity: 1;
    visibility: visible; }
  input {
    width: calc(100% - 80px);
    border: none;
    padding-right: 0;
    font-size: 0.75rem; }
  .navbar-search__btn {
    width: 40px;
    background-color: transparent;
    i {
      font-size: 1.25rem; } }
  &__close {
    width: 40px;
    background-color: transparent;
    i {
      font-size: 1.5rem; } } }
.navbar__right {
  @include d-flex;
  align-items: center;
  .author-thumb {
    width: 50px;
    height: 50px;
    @include border-radius(50%);
    overflow: hidden; }
  .follow-link-list {
    @include d-flex;
    padding-left: 15px;
    li {
      a {
        padding: 10px 7px;
        color: #777777;
        i {
          font-size: 22px; } } } } }

.pulse--primary {
  display: block;
  position: absolute;
  top: 3px;
  right: 7px;
  width: 7px;
  height: 7px;
  border-radius: 50%;
  background: $color--primary;
  cursor: pointer;
  box-shadow: 0 0 0 rgba($color--primary, 0.9);
  animation: pulse-primary 2s infinite;
  animation-duration: .9s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-out; }
@include keyframes(pulse-primary) {
  0% {
    -webkit-box-shadow: 0 0 0 0 rgba($color--primary, 0.9); }
  70% {
    -webkit-box-shadow: 0 0 0 6px rgba($color--primary, 0); }
  100% {
    -webkit-box-shadow: 0 0 0 0 rgba($color--primary, 0); } }

