.fullscreen-btn {
  background-color: transparent;
  font-size: 24px;
  @media (max-width: 575px) {
    display: none; }
  &.active {
    .fullscreen-open {
      opacity: 0;
      visibility: hidden; }
    .fullscreen-close {
      opacity: 1;
      visibility: visible; } }
  .fullscreen-open {
    transition: all 0.3s; }
  .fullscreen-close {
    margin-left: -25px;
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s; } }

.res-sidebar-open-btn {
  background-color: transparent;
  display: none;
  @media (max-width: 991px) {
    display: block; }
  i {
    font-size: 24px; } }
