/* button css start */
.button--group {
  margin: -5px -15px;
  .btn {
    margin: 5px 10px; } }
*[class*="btn-"] {
  transition: all 0.3s;
  font-size: fsize("14px"); }
.btn--capsule {
  @include border-radius(999px); }
.btn--primary {
  background-color: $color--primary;
  &:hover {
    background-color: darken($color--primary, 5%); } }
.btn--secondary {
  background-color: $color--secondary;
  &:hover {
    background-color: darken($color--secondary, 5%); } }
.btn--success {
  background-color: $color--success;
  &:hover {
    background-color: darken($color--success, 5%); } }
.btn--danger {
  background-color: $color--danger;
  &:hover {
    background-color: darken($color--danger, 5%); } }
.btn--warning {
  background-color: $color--warning;
  &:hover {
    background-color: darken($color--warning, 5%); } }
.btn--info {
  background-color: $color--info;
  &:hover {
    background-color: darken($color--info, 5%); } }
.btn--dark {
  background-color: $color--dark;
  &:hover {
    background-color: darken($color--dark, 5%); } }

.btn--primary, .btn--secondary, .btn--success, .btn--danger, .btn--warning, .btn--info, .btn--dark {
  color: #ffffff;
  &:hover {
    color: #ffffff; } }
// outline btn
.btn-outline--primary, .btn-outline--secondary, .btn-outline--success, .btn-outline--danger, .btn-outline--warning, .btn-outline--info, .btn-outline--dark {
  &:hover {
    color: #ffffff; } }
.btn-outline--primary {
  color: $color--primary;
  border-color: $color--primary;
  &:hover {
    background-color: $color--primary; } }
.btn-outline--secondary {
  color: $color--secondary;
  border-color: $color--secondary;
  &:hover {
    background-color: $color--secondary; } }
.btn-outline--success {
  color: $color--success;
  border-color: $color--success;
  &:hover {
    background-color: $color--success; } }
.btn-outline--danger {
  color: $color--danger;
  border-color: $color--danger;
  &:hover {
    background-color: $color--danger; } }
.btn-outline--warning {
  color: $color--warning;
  border-color: $color--warning;
  &:hover {
    background-color: $color--warning; } }
.btn-outline--info {
  color: $color--info;
  border-color: $color--info;
  &:hover {
    background-color: $color--info; } }
.btn-outline--dark {
  color: $color--dark;
  border-color: $color--dark;
  &:hover {
    background-color: $color--dark; } }

/* btn shadow css start */
.btn--shadow-default {
  box-shadow: 0 8px 15px 0 rgba(0, 0, 0, 0.2)!important; }
.btn--primary {
  &.btn--shadow {
    box-shadow: 0 5px 10px 0 rgba($color--primary, 0.35);
    &:hover {
      @include transform(translateY(-2px));
      box-shadow: 0 8px 15px 0 rgba($color--primary, 0.45); } } }

.btn--secondary {
  &.btn--shadow {
    box-shadow: 0 5px 10px 0 rgba($color--secondary, 0.35);
