/* btn gradient css start */
.btn--gradi {
  border: none;
  text-shadow: 1px 2px 4px rgba(0,0,0,.3); }
.btn--primary, .btn-primary {
  &.btn--gradi {
    background: #640064;
    background-image: linear-gradient(30deg,$color--primary,rgba($color--primary,.5))!important;
    text-shadow: 1px 2px 4px rgba(0,0,0,.3); } }
.btn--secondary, .btn-secondary {
  &.btn--gradi {
    background: linear-gradient(to right, #636f8e 0%, #acb8da 100%); } }
.btn--success, .btn-success {
  &.btn--gradi {
    background: #0064fa;
    background-image: linear-gradient(30deg,rgba($color--success,1),rgba($color--success,.6))!important; } }
.btn--danger, .btn-danger {
  &.btn--gradi {
    background: #640064;
    background-image: linear-gradient(30deg,rgba($color--danger,1),rgba($color--danger,.15))!important; } }
.btn--warning, .btn-warning {
  &.btn--gradi {
    background: #fafafa;
    background-image: linear-gradient(30deg,rgba($color--warning,1),rgba($color--warning,.5))!important; } }
.btn--info, .btn-info {
  &.btn--gradi {
    background: linear-gradient(30deg, lighten($color--info, 10%) 0%, darken($color--info, 15%) 100%)!important; } }
.btn--dark, .btn-dark {
  &.btn--gradi {
    background: linear-gradient(30deg, lighten($color--dark, 50%) 0%, darken($color--dark, 15%) 100%)!important; } }

/* btn gradient css end */


